import { sanity } from "@/lib/sanity";
import SendEmail from "../page";

const emailQuery = `*[_type == "emails"][0]`;

export default async function SendPage({ params }: { params: { id: string } }) {
  const email = await sanity.fetch(emailQuery, {
    id: params.id,
  });

  if (!email) {
    return <div className="p-6">Email not found</div>;
  }
  return <SendEmail emailId={email._id} />;
}
